<template>
	
	<div class="wap" >
		<div class="main" v-for="(item,index) in list">
		<label>
			<input type="checkbox" :checked="item.flag" @click="handelCheck(index)"/>
		</label>
		
		<div class="goodsImg">
			<img :src="item.img"/>
			
		</div>
		<p class="goodsName">{{item.goodsName}}</p>
		<p class="goodsPrc">{{item.price | price}} </p>
		<div class="goodsHe">
		<button class="heDel" @click="handelDle(index)">-</button>
		<input type="text" :value="item.num"/>
		<button class="heAdd" @click="handelAdd(index)">+</button>
		</div>
		<p class="goodsDan">￥<span>{{item.num | cont(item.price) | price}}</span></p>
		<p class="goodsDel" @click="han(item.id)">删除</p>
	</div>
	</div>
	
	
</template>

<script>
	import Vuex from "vuex";
	export default{
		
		filters:{
			cont(p,n){
				return (n*(p*100))/100;
			},
        price(n){
            if(!(/\./g.test(n))){
                return n+".00"
            }else{
                return n+"0";
            }
            
        }
		},
		
		computed:{
			...Vuex.mapState({
				list:state=>state.goods.list
			})
		},
		
		methods:{
			...Vuex.mapMutations({
				handelDle:'goods/handeljian',
				handelAdd:'goods/handelAdd',
				handelCheck:'goods/handelCheck'
			}),
			
			...Vuex.mapActions({
				han:"goods/handeldel"
			})
		}
		
		
	}
</script>

<style scoped>
	*{margin:0;padding:0}
	.main{
		width:1000px;
		height:120px;
		background:papayawhip;
		margin:0 auto;
		display:flex;
		
		align-items: center;
		margin-bottom: 3px;
	}
	.main>label{
		margin-left: 70px;
	}
	.main>.goodsImg>img{
		width:80px;
		height:80px;
		padding-left: 20px;
	}
	.goodsName{
		font-size: 13px;
		padding-left: 50px;
		width:80px;
	}
	.main>.goodsPrc{
		padding-left: 100px;
	}
	.main>.goodsHe{
		display:flex;
		padding-left: 110px;
	}
	.main>.goodsHe>.heDel,.heAdd{
		width:20px;
		height:20px;
		border:none;
	}
	
	.main>.goodsHe>input{
		width:40px;
		border:none;
		height:20px;
		text-align: center;
	}
	
	.main>.goodsDan{
		padding-left: 80px;
	}
	
	.main>.goodsDel{
		padding-left: 80px;
	}
</style>


