<template>
	<div class="bottom">
		
		<ul>
			<li>
				<input type="checkbox" @click="checkAll()"/>
				全选
			</li>
			<li>已选择<span>{{result.num}}</span>件商品</li>
			<li>总价:￥<span>{{result.Price}}</span></li>
			<li class="count">去结算</li>
			
			
		</ul>
	</div>
</template>

<script>
	import Vuex from "vuex";
	export default{
		
		computed:{
			...Vuex.mapGetters({
				result:"goods/result"	
			})
		},
		
		methods:{
			...Vuex.mapMutations({
				checkAll:"goods/handelAll"
			})
		}
	}
</script>

<style scoped>
	*{margin:0;padding:0}
	ul,li{
		list-style: none;
	}
	.bottom{
		width:1000px;
		height:38px;
		border:1px solid #cdcdcd;
		margin:0 auto ;
	}
	.bottom>ul{
		width:100%;
		height:38px;
		display: flex;
		justify-content: space-between;
		align-items:center
	}	
	
	.bottom>ul>li{
		font-size: 14px;
		color:#333;
	}
	.bottom>ul>.count{
		background:#c33;
		color:#fff;
		height:100%;
		line-height:38px;
		width:70px;
		text-align: center;
	}
</style>


