import axios from "axios"
export default{
	hh({commit}){
		axios({
			method:"get",
			url:"http://localhost:3000/goods"	
		}).then((data)=>{
			data.data.map((item)=>{
				item.flag = false;
			})
			//console.log(data)
			commit("handelxiu",data.data)
		})
	},
	
	handeldel({commit,dispatch},index){
		axios({
			method:"delete",
			url:"http://localhost:3000/goods/"+index
		})
		.then(()=>{
			dispatch("hh")
		})
	}
	
}



