import getters from "./getters"
export default{
	handelxiu(state,paroms){
		state.list = paroms;
		
	},
	
	handeljian(state,index){
		if(state.list[index].num <=1){
			state.list[index].num = 1;
		}else{
			state.list[index].num--
		}	
	},
	
	handelAdd(state,index){
		state.list[index].num++
	},
	
	handelCheck(state,index){
		state.list[index].flag = !state.list[index].flag;
		let bStop = true;
		state.list.map((item)=>{
			if(!item.flag){
				bStop = false;
			}
		})
		state.checkAll = bStop;
		
	},
	
	handelAll(state){
		state.checkAll = !state.checkAll;
		state.list.map((item)=>{
			item.flag = state.checkAll;
		})
	}
	
}


