<template>
	<div class="top">
		<ul>
			<li><input type="checkbox" :checked="checkAll" @click="handelcheckAll()"/>
			
			全选{{checkAll}}</li>
			<li>信息</li>
			<li>单价</li>
			<li>数量</li>
			<li>小计</li>
			<li>删除</li>
			
		</ul>
	</div>
</template>

<script>
	import Vuex from "vuex";
	export default{
		computed:{
			...Vuex.mapState({
				checkAll:state=>state.goods.checkAll
			})
		},
		
		methods:{
			...Vuex.mapMutations({
				handelcheckAll:'goods/handelAll'
			})
		}
	}
</script>

<style scoped>
	*{margin:0;padding:0}
	ul,li{
		list-style: none;
	}
	.top{
		width:1000px;
		height:38px;
		background:#CDCDCD;
		margin:50px auto 0;
	}
	.top>ul{
		width:100%;
		height:38px;
		display:flex;
		justify-content:space-around;
		align-items:center;	
	}	
	
	.top>ul>li{
		font-size: 14px;
		color:#000;
	}
</style>


