import state from './state'
export default{
	result(){
		console.log(state)
		let num =0;
		let Price = 0;
		state.list.map((item)=>{
			if(item.flag){
			num +=item.num;
			Price +=(item.num * (item.price*100))/100;
			}
		})
		
		return {
			num,
			Price
		}
	}
}


